'use client'
import React, { useState, useEffect } from 'react'
import { useSelection } from '../hooks/useSelection'

interface Props {
  iframeRef: React.RefObject<HTMLIFrameElement>
}

export default function PropertiesPanel({ iframeRef }: Props) {
  const { selectedElementId } = useSelection()
  const [attrs, setAttrs] = useState<{ tagName?: string; html?: string; src?: string; alt?: string } | null>(null)

  useEffect(() => {
    if (!selectedElementId) {
      setAttrs(null)
      return
    }
    const doc = iframeRef.current?.contentDocument
    if (!doc) return
    const el = doc.querySelector(`[data-id="${selectedElementId}"]`) as HTMLElement | null
    if (!el) return
    const isImg = el.tagName.toLowerCase() === 'img'
    setAttrs({
      tagName: el.tagName,
      html: isImg ? undefined : el.innerHTML,
      src: isImg ? (el as HTMLImageElement).src : undefined,
      alt: isImg ? (el as HTMLImageElement).alt : undefined,
    })
  }, [selectedElementId, iframeRef])

  if (!selectedElementId)
    return (
      <div className="p-6 h-full bg-gray-50 border-l border-gray-200 flex items-center justify-center text-gray-500">
        No element selected
      </div>
    )

  return (
    <div className="p-5 border-l border-gray-200 h-full bg-gray-50 overflow-y-auto">
      <h3 className="font-semibold text-gray-800 text-lg mb-2">Properties</h3>
      <p className="text-sm text-gray-600 mb-3">
        <strong>Selected:</strong> {attrs?.tagName}
      </p>

      <div className="space-y-4">
        {attrs?.src !== undefined ? (
          <ImageProperties iframeRef={iframeRef} elId={selectedElementId} />
        ) : (
          <TextProperties iframeRef={iframeRef} elId={selectedElementId} />
        )}
      </div>
    </div>
  )
}

// -------------------------------------------------------------
// ‚úçÔ∏è Text Element Properties
// -------------------------------------------------------------
function TextProperties({
  iframeRef,
  elId,
}: {
  iframeRef: React.RefObject<HTMLIFrameElement>
  elId: string
}) {
  const [text, setText] = useState('')

  useEffect(() => {
    const doc = iframeRef.current?.contentDocument
    const el = doc?.querySelector(`[data-id="${elId}"]`) as HTMLElement | null
    if (!el) return

    // Clone node & remove nested data-id attributes before showing
    const clone = el.cloneNode(true) as HTMLElement
    clone.querySelectorAll('[data-id]').forEach((n) => n.removeAttribute('data-id'))
    setText(clone.innerHTML.trim())
  }, [elId, iframeRef])

  const apply = () => {
    const doc = iframeRef.current?.contentDocument
    const el = doc?.querySelector(`[data-id="${elId}"]`) as HTMLElement | null
    if (el) el.innerHTML = text
  }

  return (
    <div className="space-y-3">
      <label className="text-sm font-medium text-gray-700">Edit Text</label>
      <textarea
        value={text}
        onChange={(e) => setText(e.target.value)}
        className="w-full h-[200px] border border-gray-300 rounded-lg p-2 text-sm font-mono focus:ring-2 focus:ring-blue-500 focus:outline-none bg-white resize-none"
      />
      <button
        onClick={apply}
        className="w-full bg-blue-500 hover:bg-blue-600 text-white rounded-md py-2 text-sm font-medium transition-all"
      >
        ‚úÖ Apply Text
      </button>
    </div>
  )
}

// -------------------------------------------------------------
// üñºÔ∏è Image Element Properties
// -------------------------------------------------------------
function ImageProperties({
  iframeRef,
  elId,
}: {
  iframeRef: React.RefObject<HTMLIFrameElement>
  elId: string
}) {
  const [src, setSrc] = useState('')
  const [alt, setAlt] = useState('')

  useEffect(() => {
    const doc = iframeRef.current?.contentDocument
    const el = doc?.querySelector(`[data-id="${elId}"]`) as HTMLImageElement | null
    setSrc(el?.src ?? '')
    setAlt(el?.alt ?? '')
  }, [elId, iframeRef])

  const apply = () => {
    const doc = iframeRef.current?.contentDocument
    const el = doc?.querySelector(`[data-id="${elId}"]`) as HTMLImageElement | null
    if (!el) return
    el.src = src
    el.alt = alt
  }

  return (
    <div className="space-y-3">
      <label className="text-sm font-medium text-gray-700">Image Source</label>
      <input
        className="w-full border border-gray-300 rounded-lg p-2 text-sm focus:ring-2 focus:ring-blue-500 focus:outline-none"
        value={src}
        onChange={(e) => setSrc(e.target.value)}
        placeholder="Image URL or Base64"
      />

      <label className="text-sm font-medium text-gray-700">Alt Text</label>
      <input
        className="w-full border border-gray-300 rounded-lg p-2 text-sm focus:ring-2 focus:ring-blue-500 focus:outline-none"
        value={alt}
        onChange={(e) => setAlt(e.target.value)}
        placeholder="Alternative text"
      />

      <button
        onClick={apply}
        className="w-full bg-green-500 hover:bg-green-600 text-white rounded-md py-2 text-sm font-medium transition-all"
      >
        ‚úÖ Apply Changes
      </button>
    </div>
  )
}
