'use client'
import React, { useRef } from 'react'
import { useSelection } from '../hooks/useSelection'
import { addTextBlock, addImageBlock } from '../lib/addElementService'
import { exportIframeHtml } from '../lib/htmlService'

interface ToolbarProps {
  iframeRef: React.RefObject<HTMLIFrameElement>
  onImportHtml: (html: string) => void
}

export default function Toolbar({ iframeRef, onImportHtml }: ToolbarProps) {
  const { selectedElementId } = useSelection()
  const fileInputRef = useRef<HTMLInputElement | null>(null)
  const imageInputRef = useRef<HTMLInputElement | null>(null)

  // üü© Add new text
  const handleAddText = () => {
    const iframe = iframeRef.current
    if (iframe) addTextBlock(iframe, 'New Text')
  }

  // üñºÔ∏è Add image from local storage
  const handleAddImage = () => {
    imageInputRef.current?.click() // open file picker
  }

  const handleImageSelected = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0]
    if (!file) return
    const reader = new FileReader()
    reader.onload = (event) => {
      const base64 = event.target?.result as string
      const iframe = iframeRef.current
      if (iframe) addImageBlock(iframe, base64)
    }
    reader.readAsDataURL(file)
  }

  // üóëÔ∏è Delete selected
  const handleDelete = () => {
    const iframe = iframeRef.current
    if (!iframe || !selectedElementId) return
    const el = iframe.contentDocument?.querySelector(`[data-id="${selectedElementId}"]`)
    el?.remove()
  }

  // üì§ Export HTML
  const handleExport = () => {
    const iframe = iframeRef.current
    if (!iframe) return
    const html = exportIframeHtml(iframe)
    const blob = new Blob([html], { type: 'text/html' })
    const url = URL.createObjectURL(blob)
    const a = document.createElement('a')
    a.href = url
    a.download = 'poster.html'
    a.click()
    URL.revokeObjectURL(url)
  }

  // üìÅ Import HTML file
  const handleFileUpload = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0]
    if (!file) return
    const reader = new FileReader()
    reader.onload = (event) => {
      const content = event.target?.result as string
      onImportHtml(content)
    }
    reader.readAsText(file)
  }

  // ‚úèÔ∏è Paste HTML manually
  const handlePasteHtml = () => {
    const pasted = prompt('Paste your HTML code below:')
    if (pasted) onImportHtml(pasted)
  }

  return (
    <div className="flex flex-col gap-2 p-3 border-r border-gray-200 min-w-[180px]">
      <h2 className="font-semibold text-gray-800 mb-2">Toolbar</h2>

      <button
        onClick={handleAddText}
        className="bg-blue-500 hover:bg-blue-600 text-white rounded px-3 py-2"
      >
        ‚ûï Add Text
      </button>

      <button
        onClick={handleAddImage}
        className="bg-indigo-500 hover:bg-indigo-600 text-white rounded px-3 py-2"
      >
        üñºÔ∏è Add Image
      </button>

      {/* hidden image file input */}
      <input
        type="file"
        accept="image/*"
        ref={imageInputRef}
        onChange={handleImageSelected}
        className="hidden"
      />

      <button
        onClick={handleDelete}
        disabled={!selectedElementId}
        className={`rounded px-3 py-2 text-white ${
          selectedElementId ? 'bg-red-500 hover:bg-red-600' : 'bg-gray-400 cursor-not-allowed'
        }`}
      >
        üóëÔ∏è Delete Selected
      </button>

      <hr className="my-3" />
      <h3 className="font-medium text-gray-700">Import / Export</h3>

      {/* hidden html file input */}
      <input
        type="file"
        accept=".html"
        ref={fileInputRef}
        onChange={handleFileUpload}
        className="hidden"
      />
      <button
        onClick={() => fileInputRef.current?.click()}
        className="bg-yellow-500 hover:bg-yellow-600 text-white rounded px-3 py-2"
      >
        üìÇ Import HTML File
      </button>

      <button
        onClick={handlePasteHtml}
        className="bg-amber-400 hover:bg-amber-500 text-white rounded px-3 py-2"
      >
        ‚úèÔ∏è Paste HTML Code
      </button>

      <button
        onClick={handleExport}
        className="bg-green-500 hover:bg-green-600 text-white rounded px-3 py-2 mt-4"
      >
        üì§ Export HTML
      </button>
    </div>
  )
}
